import Vue from 'vue'

import Router from 'vue-router'

import Welcome from '../components/Welcome'

import Install from '../components/installation/Install'


Vue.use(Router)

const routes = [
	{
		path:'/',
		component:Welcome
	},
	{
		path:'/intallation-vue',
		name:'install',
		component:Install
	}
] 

export default new Router({
	mode:'history',
	routes
})